import React from 'react'
import style from './About.module.css'
import { Helmet } from 'react-helmet'



export default function About() {
  return (
    <>
    <Helmet>
     <title>CimaMovie~About</title>
      </Helmet>
    <section className={style.about}>
    <div className={style.layer}>
      <div className='px-5 mx-5 fw-bold'>
      <h1 >When youre here, <br /> youre home.</h1>
      <p className='fs-4'>(And so is all your entertainment.)</p>
      </div>

    </div>
    </section>
    
    <div className='row align-items-center  gx-5  '>
      <div className='col-md-12'>
        <div className={style.aboutinfo}>
        <div>
          <h1 className='text-center text-white fw-bold pb-3'>Were fans at heart.</h1>
          <h4 className='text-center text-white-50' >Of movies, shows, music, and well, all things entertainment. So much so that we built an app that brings it all togetherstreaming services, personal media, ratings and watch lists. As beautiful as it is easy-to-use, el-Movies gives fans everywhere a way to discover, save, and enjoy the entertainment they love the most.</h4>
        </div>
        </div>
      </div>
    </div>
    
    <footer  >
    <hr />
       <div className='row '>
        <div className="col-md-5">
          <div className='pt-3 px-3'>
            <h1>CimaMovie~</h1>
            <p className='text-white-50'>Were on a mission to create a global community where everyone can discover, experience, and share all of the entertainment that matters to them.</p>
          </div>
        </div>
        <div className="col-md-7">
          <div className=' text-center'>
            <h1>Search Now</h1>
        <form className="d-flex" role="search">
        <input className="form-control me-2" type="search" placeholder="Movie Name" aria-label="Search" />
        </form>
       
          </div>
        </div>
       </div>
      <div className='text-center py-3 '>
      <div>
        <p>Copyright  2022 CimaMovie~. All Rights Reserved.</p>
        <span className=' fs-5 px-2'>
          <i className='fa-brands fa-facebook'></i>

          </span>
        
        <span className=' fs-5 px-2'>
        <i className='fa-brands fa-twitter'></i>

        </span>
        
        <span className=' fs-5 px-2'>
        <i className='fa-brands fa-google'></i>

        </span>
        
      </div>
      
      </div>
    </footer>
    
    </>
  )
}
